body {
  
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
}


h1 {
  text-align: center;
  font-size: 3rem;
  margin-top: 20px;
}

h4 {
  text-align: center;
  font-size: 1rem;
  margin-bottom: 20px;
  color: lightgray;
}

.search-bar {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.search-bar input, .search-bar button {
  padding: 12px;
  border-radius: 20px;
  margin: 0 5px;
  font-size: 1.2rem;
}

.search-bar button {
  background-color: #4caf50;
  border: none;
  color: white;
}

.main-section {
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
}

.daily-forecast {
  background: rgba(0, 0, 0, 0.5);
  padding: 20px;
  border-radius: 15px;
}

.forecast-item {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
  font-size: 1.3rem;
}

.today-weather {
  background: rgba(255, 255, 255, 0.3);
  padding: 30px;
  border-radius: 15px;
  text-align: center;
  font-size: 1.5rem;
  width: 300px;
}
